import React from 'react';
import './categorySection.scss';
import { Box, Grid } from '@material-ui/core';
import { OrderDetail } from 'src/components/base';
import { ArrowRightGold } from 'src/assets/icons';

const CategorySection = (props) => {
  const {
    index,
    title,
    listData,
    branch,
    onAddItemClick,
    updateListSelectedHandle,
    seeAllHandle,
  } = props;
  return (
    <Grid container lg={12} className="category-section">
      <div className="header">
        <Box display="flex">
          <div
            style={{
              width: '4px',
              backgroundColor: '#EABFB9',
              marginRight: '20px',
            }}
          />
          <p className="category-title">{title}</p>
        </Box>
        <a onClick={() => seeAllHandle(index)} className="see-all">
          <p className="see-all-txt">Xem tất cả</p>
          <ArrowRightGold />
        </a>
      </div>

      <Grid container lg={12} spacing={3}>
        {listData.map((el) => (
          <OrderDetail
            item={el}
            branch={branch}
            onAddItemClick={onAddItemClick}
            updateListSelectedHandle={updateListSelectedHandle}
          />
        ))}
      </Grid>
    </Grid>
  );
};

export default CategorySection;
