import React from 'react';
import './OrderDetail.scss';
import { Box, Container, Grid } from '@material-ui/core';
import { Breadcrumb, TitleCustom } from 'src/components/base';
import CategorySection from '../components/categorySection';
import SelectedComponent from '../components/selectedComponent';

const OrderDetailView = (props) => {
  const {
    branch,
    listMenu,
    onAddItemClick,
    updateListSelectedHandle,
    listSelected,
    onBookingClick,
    seeAllHandle,
  } = props;
  console.log('listSelected', listSelected);
  return (
    <div className="order-detail-wrapper">
      <Breadcrumb
        items={[
          { name: 'Gọi món', url: '/order' },
          { name: branch.name, url: `/order/${branch.id}`, state: { branch } },
        ]}
      />
      <Grid container>
        <Container maxWidth="lg" className="order-detail-container">
          <Box style={{ marginTop: '30px', marginBottom: '30px' }}>
            <TitleCustom title={branch.name} />
          </Box>
          {listSelected?.length > 0 ? (
            <SelectedComponent
              listSelected={listSelected}
              updateListSelectedHandle={updateListSelectedHandle}
              onBookingClick={onBookingClick}
              branch={branch}
            />
          ) : null}

          <div>
            {listMenu.map((el, idx) => (
              <CategorySection
                index={idx}
                branch={branch}
                title={el.name}
                listData={listMenu[idx].data}
                listSelected={listSelected}
                onAddItemClick={onAddItemClick}
                updateListSelectedHandle={updateListSelectedHandle}
                seeAllHandle={seeAllHandle}
              />
            ))}
          </div>
        </Container>
      </Grid>
    </div>
  );
};

export default React.memo(OrderDetailView);
