import React from 'react';
import './selectedItem.scss';
import { CounterInput } from 'src/components/base';
import { makeStyles } from '@material-ui/core/styles';
import { convertCurrency } from 'src/utils/currency';
import classNames from 'classnames';

const useStyles = makeStyles(() => {
  return {
    buttonCustom: {
      height: '25px !important',
    },
  };
});

const SelectedItem = (props) => {
  const classes = useStyles();
  const { item, borderNone, updateListSelectedHandle } = props;
  console.log('🚀 ~ file: index.js ~ line 17 ~ SelectedItem ~ item', item);

  const getQuantity = (quantity) => {
    updateListSelectedHandle({ id: item.id, quantity });
  };

  return (
    <div
      className={classNames('selected-item-container', {
        'border-none': borderNone,
      })}
    >
      <p className="name-txt">{item?.name}</p>
      <p className="name-txt">{convertCurrency(item?.price)} VNĐ</p>
      <CounterInput
        handleDecrement={() => {
          if (item?.quantity > 0) {
            getQuantity(item?.quantity - 1);
          }
        }}
        handleIncrement={() => {
          getQuantity(item?.quantity + 1);
        }}
        customStyle={classes.buttonCustom}
        value={item?.quantity || null}
      />
    </div>
  );
};

export default SelectedItem;
