/* eslint-disable no-param-reassign */
import React from 'react';
import { convertCurrency } from 'src/utils/currency';

import './selectedComponent.scss';
import { Grid } from '@material-ui/core';
import SelectedItem from '../selectedItem';
import RequestModal from '../requestModal';

const SelectedComponent = (props) => {
  const { listSelected, updateListSelectedHandle, branch } = props;
  const totalQuantity = listSelected.reduce((acc, cur) => {
    return (acc += cur.quantity);
  }, 0);

  return (
    <div className="selected-component-container">
      <Grid container>
        <p className="header-title">ĐÃ CHỌN</p>
        <Grid item lg={9} sm={12} xs={12}>
          <div className="header-table">
            <p className="table-name  food_name_title">Tên món</p>
            <p className="table-name">Đơn giá</p>
            <p className="table-name">Số lượng</p>
          </div>

          <div className="main-content-wrapper">
            <div className="main-content">
              {listSelected.map((el, idx) => (
                <SelectedItem
                  item={el}
                  borderNone={listSelected.length === idx + 1}
                  updateListSelectedHandle={updateListSelectedHandle}
                />
              ))}
            </div>
          </div>
        </Grid>
        <div className="divider_line" />
        <Grid item lg={3} sm={12} xs={12}>
          <div className="request-container">
            <p className="title-request">Số lượng</p>
            <div className="row-section">
              <p className="quantity-txt">Số món</p>
              <p className="quantity-value">x {listSelected.length}</p>
            </div>
            <div
              style={{ width: '100%', height: '1px', backgroundColor: 'black' }}
            />
            <div className="row-section">
              <p className="quantity-txt">Số phần</p>
              <p className="quantity-value">x {totalQuantity}</p>
            </div>
            <div
              style={{ width: '100%', height: '1px', backgroundColor: 'black' }}
            />
            <div className="row-section">
              <p className="quantity-txt">Tạm tính</p>
              <p className="temp-total-money">
                {convertCurrency(
                  listSelected.reduce((total, current) => {
                    return total + current.price * current.quantity;
                  }, 0)
                )}{' '}
                VNĐ
              </p>
            </div>
            <div className="row-section">
              <p className="note-txt">
                Có thể phát sinh thêm chi phí trong quá trình sử dụng dịch vụ
                tại nhà hàng
              </p>
            </div>
            {/* <button onClick={onBookingClick}>ok</button> */}
            <RequestModal listSelected={listSelected} branch={branch} />
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

export default React.memo(SelectedComponent);
