/* eslint-disable no-plusplus */
import React, { useState, useEffect } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import {
  addListSelectedHandle,
  updateListSelectedHandle,
  getListFoodsMenuHandle,
} from 'src/redux/actions/order.action';
import { getListSelectedSelector } from 'src/redux/selectors/order.selector';
import { bindActionCreators } from 'redux';
import { addModal } from 'src/redux/actions/ui.action';
import { MODAL_TYPE } from 'src/constants';
import { useHistory } from 'react-router-dom';
import { getAccessTokenSelector } from 'src/redux/selectors/auth';
import OrderDetailView from './OrderDetail.view';

const menuTypes = [
  {
    id: '01',
    type: { is_hot: true },
    name: 'Món bán chạy',
    data: [],
    total: 0,
  },
  { id: '02', type: { is_new: true }, name: 'Món mới', data: [], total: 0 },
  { id: '03', type: { is_drink: true }, name: 'Đồ uống', data: [], total: 0 },
];

const OrderDetailContainer = (props) => {
  const dispatch = useDispatch();
  const accessToken = useSelector(getAccessTokenSelector);
  const [listMenu, setListMenu] = useState(menuTypes);
  const history = useHistory();

  useEffect(() => {
    const branch = props?.location?.state?.branch || {};
    for (let i = 0; i < menuTypes.length; i++) {
      dispatch(
        getListFoodsMenuHandle(
          1,
          6,
          { ...menuTypes[i].type, branch_id: branch.id },
          (obj) => {
            const { menu, total } = obj;
            const temp = [...listMenu];

            temp[i].data = menu;
            temp[i].total = total;

            setListMenu(temp);
          }
        )
      );
    }
  }, []);

  const branch = props?.location?.state?.branch || {};

  const seeAllHandle = (index) => {
    const temp = [...listMenu];

    if (temp[index].data.length < temp[index].total) {
      const pageSize = temp[index].total - 6;

      dispatch(
        getListFoodsMenuHandle(
          7,
          pageSize,
          { ...menuTypes[index].type, branch_id: branch.id },
          (obj) => {
            const { menu, total } = obj;

            temp[index].data = [...temp[index].data, ...menu];
            temp[index].total = total;
            console.log('temppp1', temp[index].data);
            setListMenu(temp);
          }
        )
      );
    }
    history.push(`/order/see-all/${index}`, {
      branch,
      type: menuTypes[index].type,
    });
  };

  const onAddItemClick = (data) => {
    if (!accessToken) {
      dispatch(
        addModal({
          type: MODAL_TYPE.FORCE_USER_LOGIN,
        })
      );
    } else {
      props.addListSelectedHandle(data);
    }
  };

  const onBookingClick = () => {
    if (accessToken) {
      dispatch(
        addModal({
          type: MODAL_TYPE.MODAL_BOOKING_FOOD,
          order: props.listSelected,
        })
      );
    } else {
      dispatch(
        addModal({
          type: MODAL_TYPE.FORCE_USER_LOGIN,
        })
      );
    }
  };

  console.log('OrderDetailView');

  return (
    <OrderDetailView
      branch={branch}
      listMenu={listMenu}
      onAddItemClick={onAddItemClick}
      onBookingClick={onBookingClick}
      seeAllHandle={seeAllHandle}
      updateListSelectedHandle={(data) => {
        if (!accessToken) {
          dispatch(
            addModal({
              type: MODAL_TYPE.FORCE_USER_LOGIN,
            })
          );
        } else {
          props.updateListSelectedHandle(data);
        }
      }}
      listSelected={props.listSelected}
    />
  );
};

const mapStateToProps = (state) => ({
  listSelected: getListSelectedSelector(state),
});
const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    { addListSelectedHandle, updateListSelectedHandle },
    dispatch
  );
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(OrderDetailContainer);
